const getLatLongRAJANT = async () => {
    await axios.get(CONSTAPIS.APIS.maps.getAllLatLngRAJ).then((response)=>{
        let i = 0
        let rajantfeatures:any = [];
        let finalRajant:any = {}
        elementRAJANT.value = response.data;
        elementRAJANT.value.forEach((element:EQUIPAMENTS) => {

            const latitud   = element.latitud;
            const longitud  = element.longitud;
            let finalLng    = 0;
            let finalLat    = 0;

            if(latitud) {
                const gradFinalLat  = latitud.slice(0,2);                       
                const minuFinalLat  = latitud.slice(2,-1);
                const positionLat   = latitud.slice(-1);
                const lat           = parseFloat(gradFinalLat) + (parseFloat(minuFinalLat)/60);
                finalLat            = positionLat === 'S' ? -1 * lat :  lat;
            }
            
            if(longitud){
                const gradFinalLng  = longitud.slice(0,3);                       
                const minuFinalLng  = longitud.slice(3,-1);
                const positionLng   = longitud.slice(-1);
                const lng           = parseFloat(gradFinalLng) + (parseFloat(minuFinalLng)/60);
                finalLng            = positionLng === 'W' ? -1 * lng :  lng;
            }

            // let position    = {lat: finalLat, lng: finalLng}; 
            // let position    = [finalLat, finalLng]; 
            // markersrajant.value.push({
            //         position    : position,
            //         label       : element.hostname,
            //         name        : element.hostname,
            //         ip          : element.ip,
            //         tipo        : element.tipo,
            //         img         : EQUIPMENT_IMGS[element.tipo??''],
            //         size        : EQUIPMENT_SIZE[element.tipo??''],
            //     })

            if(element.tipo === 'Solar Trailer'){
                let position    = [finalLng, finalLat]; 

                rajantfeatures.push({
                    'type': 'Feature',
                    'properties':{
                        'name': element.name,
                        'index'   : `rajant${i}`,
                        'img': '/assets/Images/apt.png'
                    },
                    'geometry':{
                        'type': 'Point',
                        'coordinates': position,
                    },
                })
                
            }
        }) 
        // console.log(rajantfeatures)

        finalRajant = {
            'type' : 'FeatureCollection',
            'features' : rajantfeatures
        }

        map.addSource('finalRajant', {
            'type': 'geojson',
            'data': finalRajant
        });

        finalRajant.features.forEach(async (feature:any, index:number) => {
            // const nameRAJANT = feature.properties['name'];
            const layerID = feature.properties.index; 
            // const image = await map.loadImage(feature.properties.img);
            const image = await map.loadImage('/assets/Images/apt.png');
            map.addImage(`rajant${index}`, image.data);

            // if(!map.getLayer(layerID)){
                map.addLayer({
                    'id': layerID,
                    'type': 'symbol',
                    'source': 'finalRajant',
                    'layout': {
                        'icon-image': `rajant${index}`,
                        'icon-size': 0.35,
                        'icon-overlap': 'always',
                        'text-field': ['get', 'name'],
                        'text-font': [
                            'Open Sans Bold',
                            'Arial Unicode MS Bold'
                        ],
                        'text-size': 10,
                        'text-transform': 'uppercase',
                        'text-letter-spacing': 0.05,
                        'text-offset': [0, 4.5],
                        'text-writing-mode' : ['vertical']
                    },
                    'paint': {
                        'text-color': '#fff',
                        'text-halo-color': '#ff0000',
                        'text-halo-width': 2,
                        // "line-color": "#198EC8"
                    },
                    // 'filter': ['==', 'name', nameRAJANT]
                });
            // }
        })

        
        
        // markersrajant.value.forEach((e:any,index:number) => {
        //     const coordinates = e.position.slice()
        //     const el = document.createElement('div');
        //     el.className = 'marker';
        //     el.style.backgroundImage = `url("/assets/Images/apt.png")`;
        //     el.style.backgroundSize = `cover`;
        //     el.style.width = `60px`;
        //     el.style.height = `60px`;
        //     // el.classList.add('class-text-marker')

        //     const estilo = document.createElement('style');
        //     estilo.innerHTML = `
        //     .marker:nth-child(${index}):before {
        //         position: absolute;
        //         top: 60%;
        //         width: 100px;
        //         content: '${e.name} ';
        //         color: black;
        //         margin-top: 20px; 
        //         font-size: 12px;
        //         }
        //         `;
