/**
 * Get PMPAP Data
 */
const getLatLongPMPAP = async () => {
    await axios.get(CONSTAPIS.APIS.maps.getAllLatLngAP).then((response)=>{
            
        let itemsLatLng = response.data.body;
        let i = 0
        let pmpapfeatures:any = [];
        let finalPMPAP:any = {}

        itemsLatLng.forEach((element:EQUIPAMENTS) => {
            i++;
            let latitud     = parseFloat(element.latitud??'0');
            let longitud    = parseFloat(element.longitud??'0');

            if(element.hostname?.includes( CONSTSTRINGS.STRINGS_LABELS.PMPAP1 ) || 
                element.hostname?.includes( CONSTSTRINGS.STRINGS_LABELS.PMPAP5 ) ){
                longitud    = parseFloat(element.longitud??'0') + 0.000512,
                latitud     = parseFloat(element.latitud??'0') - 0.000512
            }    
            
            pmpapfeatures.push({
                'type': 'Feature',
                'properties':{
                    'name': element.hostname,
                    'index'   : `pmpap${i}`,
                    'img': '/assets/Images/pmp-ap.png'
                },
                'geometry':{
                    'type': 'Point',
                    'coordinates': [longitud, latitud]
                },
            })

            // markerspmpap.value.push({
            //     position: [longitud, latitud],
            //     name    : element.hostname,
            //     ip      : element.ip,
            //     tipo    : element.tipo,
            //     index   : i
            // })
            
        }) 

        finalPMPAP = {
            'type' : 'FeatureCollection',
            'features' : pmpapfeatures
        }

        map.addSource('finalPMPAP', {
            'type': 'geojson',
            'data': finalPMPAP
        });

        finalPMPAP.features.forEach(async (feature:any, index:number) => {
            const namePMPAP = feature.properties['name'];
            const layerID = feature.properties.index; 
            const image = await map.loadImage('/assets/Images/pmp-ap.png');
            map.addImage(`pmpap${index}`, image.data);

            if(!map.getLayer(layerID)){
                map.addLayer({
                    'id': layerID,
                    'type': 'symbol',
                    'source': 'finalPMPAP',
                    'layout': {
                        'icon-image': `pmpap${index}`,
                        'icon-size': 0.20,
                        'icon-overlap': 'always',
                        'text-field': ['get', 'name'],
                        'text-font': [
                            'Open Sans Bold',
                            'Arial Unicode MS Bold'
                        ],
                        'text-size': 10,
                        'text-transform': 'uppercase',
                        'text-letter-spacing': 0.05,
                        'text-offset': [0, 4.5],
                    },
                    'paint': {
                        'text-color': '#202',
                        'text-halo-color': '#fff',
                        'text-halo-width': 2
                    },
                    'filter': ['==', 'name', namePMPAP]
                });
            }
        })

        

    })
}

const matchPMPSMwRAJANT = async() =>{
    
    elementPMPSM.value.forEach((elem:EQUIPAMENTS)=>{


        // let latSM = (`${elem.latitud?.slice(0,2)}.${elem.latitud?.slice(2)}`)??'0';
        // let lngSM = (`${elem.longitud?.slice(0,4)}.${elem.longitud?.slice(4)}`)??'0';
        // var positionF = ref({lat: parseFloat(latSM), lng: parseFloat(lngSM)});
        
        
        // let latSM = (`${elem.latitud?.slice(0,2)}.${elem.latitud?.slice(2)}`)??'0';
        // let lngSM = (`${elem.longitud?.slice(0,4)}.${elem.longitud?.slice(4)}`)??'0';
        // var positionF = ref({lat: parseFloat(latSM), lng: parseFloat(lngSM)});
        
        // var positionF = ref({lat: 0, lng:0});
        var positionF:Ref<number[]> = ref([0,0]);
        if(elem.latitud && elem.longitud){
            // positionF = ref({lat: parseFloat(elem.latitud), lng: parseFloat(elem.longitud)});
            positionF = ref([ parseFloat(elem.latitud), parseFloat(elem.longitud) ]);
        }
        // markersrajant.value.find(({name,position}:MARKERS)=>{
        markersrajant.value.find(({name,position}:any)=>{

            if(name && position){
                if(name.split('-')[0] === elem.hostname?.split('M')[1]){
                    positionF.value  = position;
                }
            }
        })

        markerspmpsm.value.push({
            position: positionF.value,
            name    : elem.hostname,
            ip      : elem.ip,
            tipo    : elem.tipo,
            padre   : elem.padre,
        })
    })
}

