const getFinalPositions = (lat:number, lng:number) => {
    let lat1 = lat + 0.001;
    let lng1 = lng + 0.001;
    let finalItem = [
        [ lng,  lat  ],
        [ lng1, lat  ],
        [ lng1, lat1 ],
        [ lng,  lat1 ],
        [ lng,  lat  ],
    ]
    return finalItem
}

