                    // })

                    finalTestObj.value.push({                        
                            "type": "Feature",
                            "properties": {
                                "level": 0,
                                "name": "Test",
                                "height": 100,
                                "base_height": 0,
                                "color": color,
                                "center": [lng,lat],
                                "latency": item.latency.toFixed(0)
                            },
                            "geometry": {
                                "coordinates": [ getFinalPositions(lat,lng) ],
                                "type": "Polygon"
                            },
                            // 'id': '06e8fa0b3f851e3ae0e1da5fc17e111e' 
                            'id': index 
                    })
                    // allCircles.value.push({
                    //     fecha   : item.fecha??'',
                    //     color   : 'green',
                    //     valores : {
                    //         rssi        : item.rssi,
                    //         rsrp        : item.rsrp,
                    //         rsrq        : item.rsrq
                    //     },
                    //     options: {
                    //         center          : {lat: parseFloat(item.lat??'0'), lng: parseFloat(item.lon??'0')},
                    //         radius          : 3,
                    //         strokeColor     : 'green',
                    //         strokeOpacity   : 0.8,
                    //         strokeWeight    : 2,
                    //         fillColor       : 'green',
                    //         fillOpacity     : 0.50,
                    //     }
                    // })
                })
            }
            else{
                disableSearchTruck.value = false;
                finalItems.forEach((item:TRACKING) => {
                    let color = getColorByPort(item.port??'');
                    allCircles.value.push({
                        fecha   : item.fecha??'',
                        color   : color,
                        valores : { 
                            cost        : item.cost,
                            ipv4Address : item.ipv4Address 
                        },
                        options: {
                            center          : [parseFloat(item.lat??'0'), parseFloat(item.lon??'0')],
                            // center          : { lat: parseFloat(item.lat??'0'), lng: parseFloat(item.lon??'0') },
