}

const getFinalCubes = async () => {
    const mySource = map.getSource('locationHaultruck')
    // console.log(litTestObj.value)       
    if(mySource === undefined){ 
        map.addSource('locationHaultruck', {
            'type': 'geojson',
            'data': litTestObj.value,
        });
        // map.addSource('locationHaultruck', {
        //     'type': 'vector',
        //     'data': litTestObj.value,
        // });
        map.addLayer({
            'id': 'room-extrusion',
            'type': 'fill-extrusion',
            'source': 'locationHaultruck',
            'paint': {
                'fill-extrusion-color': ['get', 'color'],
                'fill-extrusion-height': ['get', 'height'],
                'fill-extrusion-base': ['get', 'base_height'],
                'fill-extrusion-opacity':1,
            }
        });

        const popup = new maplibregl.Popup({
            closeButton: false,
            closeOnClick: false
        });

        map.on('mouseenter', 'room-extrusion', (e:any) => {
            map.getCanvas().style.cursor = 'pointer';

            const coordinates:any = [e.lngLat.lng, e.lngLat.lat];
            const latency = e.features[0].properties.latency ;

            while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
            }

            // popup.setLngLat(coordinates).setHTML(`${coordinates[0].toFixed(4)}, ${coordinates[1].toFixed(4)} => ${latency}`).addTo(map);
            popup.setLngLat(coordinates).setHTML(`Latencia: ${latency}ms`).addTo(map);
        });

        map.on('mouseleave', 'room-extrusion', () => {
            map.getCanvas().style.cursor = '';
            popup.remove();
        });
    }
    else{
        mySource.setData(litTestObj.value)
    }

}

const filterByStatus = async (arrayStatus:string[]) => {
    allCirclesFiltered.value = [];

    allCircles.value.forEach((circle:any) => {
    // allCircles.value.forEach((circle:CIRCLE) => {
        if(isDrive.value){
            allCirclesFiltered.value.push(circle)
        }
        else{
            if(arrayStatus.includes(circle.color)){
                allCirclesFiltered.value.push(circle)
            }
        }
    })
    await showCircles(arrayDatesCircle.value[indexFrom.value],arrayDatesCircle.value[indexTo.value]);
}

