const getColorByPort = (port:string) => {
    let finalcolor = '#FF00FF';
    if(port === 'rpt' )     finalcolor = '#03A9F4';
    if(port === 'wlan0' )   finalcolor = '#388E3C';
    if(port === 'wlan1' )   finalcolor = '#66BB6A';
    if(port === 'wlan2' )   finalcolor = '#FFEB3B';
    if(port === 'wlan3' )   finalcolor = '#FF9800';
    return finalcolor
}
const getColorByLatency = (latency:number) => {
    let finalcolor = '#FF00FF';
    if(latency > 0 && latency <= 100 )     finalcolor = 'green';
    if(latency > 100 && latency <= 200 )     finalcolor = 'yellow';
    if(latency > 200 && latency <= 500 )     finalcolor = 'red';
    if(latency > 500 || latency <= 0 )     finalcolor = 'grey';
    return finalcolor
}

